import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  View,
  ScreenSpinner,
  AdaptivityProvider,
  AppRoot,
  Avatar,
  Panel,
  Snackbar,
  ActionSheet,
  ActionSheetItem,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import Home from "./panels/Home";

import {
  Icon56DataStackLockOutline,
  Icon28StoryOutline,
  Icon16Done,
  Icon24Linked,
} from "@vkontakte/icons";
import background from "./img/story.png";
import { Howl, Howler } from "howler";
let audio, audio2;

audio2 = new Howl({
  src: ["./sounds/screamer.mp3"],
  format: ["mp3"],
  volume: 0.7,
});

let id = window.location.search.split("user_id=")[1].split("&")[0];
bridge.send("VKWebAppInit");

const App = () => {
  const [baseTargetRef, setBaseTargetRef] = useState(React.createRef());
  const [Counter, setCounter] = useState(0);
  const [activePanel, setActivePanel] = useState("home");
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState(false);
  const [snackbar, setSnackbar] = useState(null);

  const makeStory = () => {
    bridge.send("VKWebAppShowStoryBox", {
      background_type: "image",
      url: "https://i.ibb.co/p2W8bFr/story.png",
      locked: true,
      attachment: {
        text: "go_to",
        type: "url",
        url: "https://vk.com/app7983450#" + id,
      },
    });
  };

  function openSnackbar() {
    setSnackbar(
      <Snackbar
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ background: "#3a0c74" }}>
            <Icon56DataStackLockOutline fill="#fff" width={14} height={14} />
          </Avatar>
        }
      >
        Для доступа к этому разделу необходимо ещё {30 - Counter} монет
      </Snackbar>
    );
  }

  function openPopout() {
    setPopout(
      <ActionSheet
        onClose={() => {
          setPopout(null);
        }}
        iosCloseItem={
          <ActionSheetItem autoclose mode="cancel">
            Отменить
          </ActionSheetItem>
        }
        toggleRef={baseTargetRef.current}
      >
        <ActionSheetItem
          autoclose
          onClick={() => {
            makeStory();
          }}
          before={<Icon28StoryOutline />}
        >
          Опубликовать историю
        </ActionSheetItem>
        <ActionSheetItem
          autoclose
          onClick={() => {
            bridge.send("VKWebAppCopyText", {
              text:
                "Зацени коин. Мне кажется, или звуки там херня? " +
                "https://vk.com/app7983450#" +
                id,
            });
            setSnackbar(
              <Snackbar
                onClose={() => setSnackbar(null)}
                action="Поделиться"
                onActionClick={() =>
                  bridge.send("VKWebAppShare", {
                    link: "https://vk.com/app7983450#" + id,
                  })
                }
                before={
                  <Avatar size={24} style={{ background: "var(--accent)" }}>
                    <Icon16Done fill="#fff" width={14} height={14} />
                  </Avatar>
                }
              >
                Ссылка скопирована
              </Snackbar>
            );
          }}
          before={<Icon24Linked width={28} height={28} />}
        >
          Отправить ссылку друзьям
        </ActionSheetItem>
      </ActionSheet>
    );
  }

  useEffect(() => {
    bridge.subscribe(({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        const schemeAttribute = document.createAttribute("scheme");
        schemeAttribute.value = data.scheme ? data.scheme : "client_light";
        document.body.attributes.setNamedItem(schemeAttribute);
      }
    });
    async function fetchData() {
      const user = await bridge.send("VKWebAppGetUserInfo");
      setUser(user);
    }

    fetchData();
  }, []);

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

  return (
    <AdaptivityProvider>
      <AppRoot>
        <View id="home" activePanel={activePanel} popout={popout}>
          <Home
            id="home"
            fetchedUser={fetchedUser}
            go={go}
            Counter={Counter}
            setCounter={setCounter}
            openSnackbar={openSnackbar}
            setActivePanel={setActivePanel}
            snackbar={snackbar}
            openPopout={openPopout}
            audio={audio}
            audio2={audio2}
          />
        </View>
      </AppRoot>
    </AdaptivityProvider>
  );
};

export default App;
