import { React } from "react";
import useSound from "use-sound";
import bridge from "@vkontakte/vk-bridge";

import "./Screamer.css";
let count = 0;

function Screamer({ setScreamer, setEnd, audio2, scaryPicture, img2 }) {
  function startScreamer() {
    if (count == 0) {
      count += 1;

      audio2.play();

      setTimeout(() => {
        setScreamer(null);
        setEnd(true);
        bridge.send("VKWebAppShowNativeAds", { ad_format: "preloader" });
      }, 2000);
    }
  }

  return (
    <div className="Screamer">
      <img2 onLoad={startScreamer()}></img2>
    </div>
  );
}

export default Screamer;
